body {
  background-color: #C0DFFF;
}

.app-wrapper {
  background-color: #C0DFFF;
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas: "h h" "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-row: c;
  min-height: 700px;
}